import './App.css'
import 'antd/dist/antd.min.css'
import React, { useEffect, useState } from 'react'
import LoginPage from './components/Login/Login'
import RegisterPage from './components/Register/Register'
import CategoryPage from './components/Products/Category/Category'
import { Link, Redirect, Route, Switch } from 'react-router-dom'
import DialogsContainer from './components/Dialogs/DialogsContainer'
import UsersContainer from './components/Users/UsersContainer'
import ProfileContainer from './components/Profile/ProfileContainer'
import { Layout, Menu, Pagination } from 'antd'
import { LaptopOutlined, MenuOutlined, NotificationOutlined, UserOutlined } from '@ant-design/icons'
import { Header } from './components/Header/Header'
import { withSuspense } from './hoc/withSuspense'
import Internationalization from './components/Internationalization/Internationalization'
import { Trans, withTranslation } from 'react-i18next'
import Advertisement from './components/Products/Advertisement/Advertisement'

import { Breakpoint, BreakpointProvider } from 'react-socks'

const { Content, Footer, Sider } = Layout
const logo = require('./assets/images/logo.jpg')

// const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
// const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));

const SuspendedDialogs = withSuspense(DialogsContainer)
const SuspendedProfile = withSuspense(ProfileContainer)

const App = (props: any) => {
  const [collapsed, setCollapsed] = useState(true)
  const { t, i18n } = props

  const [width, setWindowWidth] = useState(0)

  const updateDimensions = () => {
    const width = window.innerWidth
    setWindowWidth(width)
  }

  // componentDidMount...runs only once
  useEffect(() => {
    updateDimensions()
    window.addEventListener('resize', updateDimensions)
    return () => window.removeEventListener('resize', updateDimensions)
  }, [])

  const responsive = {
    showTopNavMenu: width > 1023,
  }

  return (
    <BreakpointProvider>
      <Layout>
        <Content style={{ padding: '0 10px' }}>
          <Layout className="site-layout" style={{ padding: '10px 0' }}>
            <Sider trigger={null} collapsible collapsed={collapsed} width={200} theme={'light'}>
              <div className="logo">
                <Link to={'/Webshop/'}>
                  <img src={logo} alt={'some text'}></img>
                </Link>
              </div>
              <Menu mode="inline" style={{ height: '100%' }}>
                <Menu.SubMenu key="sub1" icon={<UserOutlined />} title={t('Profile')}>
                  <Menu.Item key="1">
                    {' '}
                    <Link to="/Webshop/profile/">{t('Profile')}</Link>
                  </Menu.Item>
                  <Menu.Item key="2">
                    {' '}
                    <Link to="/Webshop/dialogs/">{t('Messages')}</Link>
                  </Menu.Item>
                  <Menu.Item key="3">
                    {' '}
                    <Link to="/Webshop/internationalization/">{t('Internationalization')}</Link>
                  </Menu.Item>
                </Menu.SubMenu>
                <Menu.SubMenu key="sub2" icon={<LaptopOutlined />} title={t('Users')}>
                  <Menu.Item key="5">
                    <Link to="/Webshop/users/">{t('Users')}</Link>
                  </Menu.Item>
                </Menu.SubMenu>
                <Menu.SubMenu key="sub3" icon={<MenuOutlined />} title={t('Categories')}>
                  <Menu.Item key="9">
                    <Link to="/Webshop/catalog/">{t('Catalog')}</Link>
                  </Menu.Item>
                  <Menu.Item key="10">{t('Express')}</Menu.Item>
                  <Menu.Item key="11">{t('Department store')}</Menu.Item>
                  <Menu.Item key="12">{t('Products')}</Menu.Item>
                  <Menu.Item key="13">{t('Pharmacy')}</Menu.Item>
                  <Menu.Item key="14">{t('To the children')}</Menu.Item>
                  <Menu.Item key="15">{t('The clothing')}</Menu.Item>
                  <Menu.Item key="16">{t('Electronics')}</Menu.Item>
                </Menu.SubMenu>
              </Menu>
            </Sider>
            <Layout>
              <Header collapsed={collapsed} setCollapsed={setCollapsed} i18n={i18n} t={t} />
              <Content style={{ padding: '20px 20px', minHeight: '660px' }}>
                <Switch>
                  <Route exact path="/Webshop/" render={() => <Advertisement />} />

                  <Route path="/Webshop/dialogs/" render={() => <SuspendedDialogs />} />

                  <Route path="/Webshop/profile/:userId?/" render={() => <SuspendedProfile />} />

                  <Route path="/Webshop/users/" render={() => <UsersContainer />} />

                  <Route path="/Webshop/login/" render={() => <LoginPage />} />

                  <Route path="/Webshop/register/" render={() => <RegisterPage />} />

                  <Route path="/Webshop/catalog/" render={() => <CategoryPage />} />

                  <Route
                    path="/Webshop/internationalization/"
                    render={() => <Internationalization i18n={props.i18n} />}
                  />

                  <Route path="*" render={() => <div>404 NOT FOUND</div>} />
                </Switch>
              </Content>
            </Layout>
          </Layout>
        </Content>
        <Breakpoint large up>
          <div style={{ justifyContent: 'center', textAlign: 'center', display: 'flex', flexDirection: 'column' }}>
            <Footer>Web-shop ©2022 Created by Mikhail Gorelov</Footer>
          </div>
        </Breakpoint>
      </Layout>
    </BreakpointProvider>
  )
}

export default withTranslation('translations')(App)
