import React from 'react'
import './Header.module.css'
import { HeaderLarge } from './Headers/Large/HeaderLarge'
import { HeaderSmall } from './Headers/Small/HeaderSmall'
import { HeaderMedium } from './Headers/Medium/HeaderMedium'

export type MapPropsType = {
  collapsed: any
  setCollapsed: any
  t: any
  i18n: any
}

export const Header: React.FC<MapPropsType> = (props) => {
  const { t, i18n } = props

  return (
    <>
      <HeaderLarge collapsed={props.collapsed} setCollapsed={props.setCollapsed} i18n={i18n} t={t} />
      <HeaderSmall collapsed={props.collapsed} setCollapsed={props.setCollapsed} i18n={i18n} t={t} />
      <HeaderMedium collapsed={props.collapsed} setCollapsed={props.setCollapsed} i18n={i18n} t={t} />
    </>
  )
}
