import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Avatar, Button, Col, Layout, Menu, Popover, Row } from 'antd'
import {
  HeartOutlined,
  InboxOutlined,
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  ShoppingCartOutlined,
  UserOutlined,
} from '@ant-design/icons'
import { useDispatch, useSelector } from 'react-redux'
import { selectCurrentUserLogin, selectIsAuth } from '../../../../redux/auth-selecors'
import { logout } from '../../../../redux/auth-reducer'
import { Input } from 'antd'
import './HeaderLarge.module.css'
import { Breakpoint } from 'react-socks'

const { Search } = Input

const onSearch = (value: any) => console.log(value)

export type MapPropsType = {
  collapsed: any
  setCollapsed: any
  t: any
  i18n: any
}

export const HeaderLarge: React.FC<MapPropsType> = (props) => {
  const { t, i18n } = props
  const isAuth = useSelector(selectIsAuth)
  const login: any = useSelector(selectCurrentUserLogin)

  const dispatch = useDispatch()

  const logoutCallback = () => {
    // @ts-ignore
    dispatch(logout())
  }

  const { Header } = Layout

  return (
    <Breakpoint large up>
      <Header
        className="site-layout-background"
        style={{
          padding: 0,
          width: '100%',
        }}
      >
        <Row gutter={[8, 16]}>
          <Col span={2}>
            {React.createElement(props.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
              className: 'trigger',
              onClick: () => props.setCollapsed(!props.collapsed),
            })}
          </Col>
          <Col span={11} style={{ paddingLeft: '1%', paddingRight: '6%' }}>
            <Search
              placeholder="Search products"
              bordered={true}
              size={'middle'}
              allowClear
              enterButton={t('Search')}
              onSearch={onSearch}
              style={{
                display: 'block',
                marginTop: '16px',
              }}
            />
          </Col>
          <Col span={2} style={{ marginTop: '3px' }}>
            <Link to="/">
              <InboxOutlined style={{ fontSize: '22px', color: '#08c' }} />
            </Link>
          </Col>
          <Col span={2} style={{ marginTop: '3px' }}>
            <Link to="/">
              <HeartOutlined style={{ fontSize: '22px', color: '#08c' }} />
            </Link>
          </Col>
          <Col span={2} style={{ marginTop: '3px' }}>
            <Link to="/">
              <ShoppingCartOutlined style={{ fontSize: '22px', color: '#08c' }} />
            </Link>
          </Col>
          {isAuth ? (
            <>
              {' '}
              <Col span={5}>
                <Avatar
                  alt={login || ''}
                  style={{ backgroundColor: '#87d068', marginRight: '25%' }}
                  icon={<UserOutlined />}
                />
                <Button onClick={logoutCallback} type={'primary'}>
                  <Link to="/Webshop/">Log out</Link>
                </Button>
              </Col>
            </>
          ) : (
            <Col span={5}>
              <Button type="primary">
                <Link to={'/Webshop/login/'}>{t('Login')}</Link>
              </Button>
            </Col>
          )}
        </Row>
      </Header>
    </Breakpoint>
  )
}
